$prefix: '.rise-blocks';

div[data-type="rise-blocks/button"] {
    display: inline-block;
}

.wp-block-rise-blocks-button {
    &#{$prefix}-icon-left {
       > div {
            order: 2;
        }
    }

    &#{$prefix}-icon-right {
        > div {
            order: 1;
        }
    }
}

.wp-block-rise-blocks-buttons {
    position: relative;
    z-index: 99999;

    a {
        text-decoration: none;
    }
}